


















































































































import Vue from 'vue'
import { getResourcePages } from '@/services/resource'
import { getResourceCategories } from '@/services/resource-category'
import { Form } from 'element-ui'

export default Vue.extend({
  name: 'ResourceList',
  data () {
    return {
      resources: [], // 资源列表
      form: {
        current: 1, // 默认查询第1页数据
        size: 10, // 每页大小
        name: '',
        url: '',
        categoryId: null // 资源分类，null查询所有
      },
      totalCount: 0,
      resourceCategories: [], // 资源分类列表
      isLoading: true // 加载状态
    }
  },
  created () {
    this.loadResources()
    this.loadResourceCategories()
  },
  methods: {
    async loadResourceCategories () {
      const { data } = await getResourceCategories()
      this.resourceCategories = data.data
    },
    async loadResources () {
      this.isLoading = true // 展示加载中状态
      const { data } = await getResourcePages(this.form)
      this.resources = data.data.records
      this.totalCount = data.data.total
      this.isLoading = false // 关闭加载中状态
    },
    handleEdit (item: any) {
      console.log('handleEdit', item)
    },
    handleDelete (item: any) {
      console.log('handleDelete', item)
    },
    onSubmit () {
      this.form.current = 1 // 筛选查询从第1页开始
      this.loadResources()
    },
    handleSizeChange (val: number) {
      // 每页大小发生改变
      // console.log(`每页 ${val} 条`)
      this.form.size = val
      this.form.current = 1 // 每页大小改变，重新查询第1页数据
      this.loadResources()
    },
    handleCurrentChange (val: number) {
      // 页码发生改变
      // console.log(`当前页: ${val}`)
      // 请求获取对应页码的数据
      this.form.current = val // 修改要查询的页码
      this.loadResources() // 重绘列表数据
    },
    onReset () {
      (this.$refs.form as Form).resetFields()
      this.form.current = 1 // 重置回到第1页
      this.loadResources()
    }
  }
})
